<template>
    <div class="profile">
      <header>
          <div @click="goback()"><i class="fas fa-chevron-left"></i></div>
          <h3>我的</h3>
      </header>
      <div class="login">
          <router-link to="#" v-if="this.logined">
            <div class="login-title">
                <h1>{{userInfo}}</h1>
            </div>
          </router-link>
          <router-link to="/login" v-else>
            <img src="static/elm/img/login/elm.png" alt="">
            <div class="login-title">
                <h1>登陆/注册</h1>
                <p>登陆后享更多特权</p>
            </div>
          </router-link>
      </div>
      <index-footer></index-footer>      
    </div>
</template>
<script>
import {mapState, mapMutations} from 'vuex'
const indexFooter = () => import('@/components/footer/index-footer')
export default {
    components: {
        indexFooter
    },
    data(){
        return {
            select1: true,
            select2:false,
            disabled: false,
            phoneNumber:null,
            phone: null,
            rightPhoneNumber:true,
            showPassword: true,
            name: undefined,
            passWord: null,
        }
    },
    computed: {
        ...mapState([
          'userInfo', 'logined'
        ]),
    },
    methods: {
        goback: function () {
        window.history.length > 1
            ? this.$router.go(-1)
            : this.$router.push('/')
        }
    }
}
</script>
<style scoped>
header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 15vw;
    font-size: 4vw;
    line-height: 15vw;
    text-align: center;
    color: white;
    z-index: 2;
}
header div{
    position: absolute;
    top:0;
    left: 0;
    width: 10vw;
    height: 15vw;
    background-color:transparent;
    z-index: 2;
}
header h3{
    width: 100vw;
    height: 15vw;
    background-color: rgb(35, 149, 255);
}
.login{
    margin-top: 15vw;
    height: 30vw;
    background-color: rgb(35, 149, 255);
}
.login a{
    position: relative;
    display: block;
    height: 100%;
}
.login a img{
    position: absolute;
    top: 25%;
    left: 10%;
    display: block;
    height: 50%;
    border-radius: 15vw;
}
.login-title{
    width: 40vw;
    color: rgb(255, 255, 255);
    position: absolute;
    left: 30vw;
    top: 25%;
}
</style>







